
.massage {
    display: flex;
    align-items: flex-end;
    margin: 10px;
}


.text1{
     min-width: 150px;
     max-width: 300px;
     word-wrap: break-word;
}



.avatar {
    height: 50px;
    width: 50px;
    border-radius: 50%;
}

.text {
    padding: 10px;
    border-radius: 5px 5px 5px 0;
    background-color: rgba(225,225,225,0.3);
}
.angle{
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0,0,0,0) 15px, rgba(225,225,225,0.3) 0);
}